import Card from "../ui/Card";
import classes from "./SearchResult.module.css";

function formatGender(gender) {
  return gender === "M" ? "Male" : "Female";
}

function SearchResult(props) {
  const isFemale = props.baby.gender === "F";
  return (
    <div className={classes.wrapper}>
      <h2 className={classes.heading}>
        {isFemale ? "Female Information" : "Male Information"}
      </h2>
      <Card bgColor={isFemale ? "salmon" : "blue"}>
        <div className={classes.main}>
          <div>
            <h1 className={classes.name}>{props.baby.name}</h1>
            <h2>
              <span className={classes.ranking}>{props.baby.ranking}</span>{" "}
              <em>Ranking</em>
            </h2>
          </div>

          <div>
            <h3>YOB: {props.baby.birthYear}</h3>
            <h3>Gender: {formatGender(props.baby.gender)}</h3>
            <h3>Total Births: {props.baby.totalBirths}</h3>
          </div>
        </div>
      </Card>
    </div>
  );
}

export default SearchResult;
