import Card from "../ui/Card";
import classes from "./RankingResult.module.css";

function formatGender(gender) {
  return gender.toLowerCase() === "neutral"
    ? "Neutral"
    : gender === "M"
    ? "Male"
    : "Female";
}

function RankingResult({ highestRanking, averageRanking }) {
  return (
    <div className={classes.wrapper}>
      <h2 className={classes.heading}>Ranking Information</h2>
      <Card bgColor="DarkMagenta">
        <div className={classes.main}>
          <div>
            <h1 className={classes.name}>{highestRanking.name}</h1>
            <h2>{formatGender(averageRanking.gender)}</h2>
          </div>

          <div>
            <h3>Most recent highest ranking YOB: {highestRanking.birthYear}</h3>
            <h3 className={classes.ranking}>
              <span>{highestRanking.ranking}</span> <em>Highest Ranking</em>
            </h3>
            <h3 className={classes.ranking}>
              <span>{averageRanking.averageRanking}</span>{" "}
              <em>Average Ranking</em>
            </h3>
          </div>
        </div>
      </Card>
    </div>
  );
}

export default RankingResult;
