import { Fragment, useState, useContext } from "react";
import classes from "./SearchForm.module.css";
import SearchResult from "./SearchResult";
import RankingResult from "./RankingResult";
import PopularityResults from "./PopularityResults";
import Message from "../ui/Message";
import GlobalContext from "../../stores/GlobalContext";

const startYear = 2010;
const endYear = 2014;
const years = [];

for (let year = startYear; year <= endYear; year++) {
  years.push(year);
}

function SearchForm() {
  const {
    babyName,
    setBabyName,
    babyStats,
    setBabyStats,
    popularityStats,
    setPopularityStats,
  } = useContext(GlobalContext);

  const [name, setName] = useState("");
  const [birthYear, setBirthYear] = useState("1880");
  const [gender, setGender] = useState("Neutral");
  const [hasError, setHasError] = useState(false);
  const [notFound, setNotFound] = useState(false);
  const [loadingBabyName, setLoadingBabyName] = useState(false);
  const [loadingBabyStats, setLoadingBabyStats] = useState(false);
  const [loadingPopularity, setLoadingPopularity] = useState(false);

  const SEARCH_RESULT_URL = `${process.env.REACT_APP_BASE_URL}/search/year/${birthYear}/name/${name}?exact=true&gender=${gender}`;
  const HIGHEST_RANKING_URL = `${process.env.REACT_APP_BASE_URL}/ranking/name/${name}?gender=${gender}`;
  const AVERAGE_RANKING_URL = `${process.env.REACT_APP_BASE_URL}/average/name/${name}?gender=${gender}`;
  const POPULARITY_URL = `${process.env.REACT_APP_BASE_URL}/popularity/name/${name}?gender=${gender}`;

  async function handleSubmit(e) {
    e.preventDefault();

    if (!name || !birthYear || !gender) {
      return;
    }

    setBabyName(null);
    setBabyStats(null);
    setPopularityStats(null);

    try {
      setLoadingBabyName(true);
      const searchResponse = await fetch(SEARCH_RESULT_URL);
      const searchResult = await searchResponse.json();

      if (searchResult.babies.length === 0) {
        setNotFound(true);
        setBabyName(null);
        setBabyStats(null);
        setPopularityStats(null);
        setLoadingBabyName(false);
        setTimeout(() => {
          setNotFound(false);
        }, 5000);
        return;
      }

      setBabyName(searchResult);
      setLoadingBabyName(false);

      setLoadingBabyStats(true);
      const highestRankingResponse = await fetch(HIGHEST_RANKING_URL);
      const highestRanking = await highestRankingResponse.json();

      const averageRankingResponse = await fetch(AVERAGE_RANKING_URL);
      const averageRanking = await averageRankingResponse.json();

      const values = await Promise.all([highestRanking, averageRanking]);
      setBabyStats(values);
      setLoadingBabyStats(false);

      setLoadingPopularity(true);
      const popularityResponse = await fetch(POPULARITY_URL);
      const popularity = await popularityResponse.json();
      setPopularityStats(popularity);
      setLoadingPopularity(false);
    } catch (e) {
      setHasError(true);
      setBabyName(null);
      setBabyStats(null);
      setLoadingBabyName(false);
      setLoadingBabyStats(false);
      setLoadingPopularity(false);
      setTimeout(() => {
        setHasError(false);
      }, 5000);
    }

    setName("");
  }

  return (
    <Fragment>
      <header className={classes.header}>
        <h1>Find fun facts about your name!</h1>
      </header>
      <section className={classes.main}>
        <form className={classes.form} onSubmit={handleSubmit}>
          <div className={classes.name}>
            <label htmlFor="name">First Name</label>
            <input
              value={name}
              onChange={(e) => setName(e.target.value)}
              id="name"
              placeholder="Enter a first name"
              type="text"
              required
            />
          </div>
          <div className={classes.year}>
            <label htmlFor="year">Birth Year</label>

            <select
              id="year"
              value={birthYear}
              onChange={(e) => setBirthYear(e.target.value)}
            >
              {years.map((year) => (
                <option key={year}>{year}</option>
              ))}
            </select>
          </div>
          <div className={classes.gender}>
            <label htmlFor="gender">Gender</label>

            <select
              id="gender"
              value={gender}
              onChange={(e) => setGender(e.target.value)}
            >
              <option value="neutral">Neutral</option>
              <option value="M">Male</option>
              <option value="F">Female</option>
            </select>
          </div>
          <div className={classes.submit}>
            <button type="submit">Get Info</button>
          </div>
        </form>
      </section>
      <section className={classes.searchResult}>
        {notFound && (
          <Message
            color="#6c757d"
            message={`${name} was not found in the year ${birthYear}.`}
          />
        )}
        {hasError && (
          <Message
            color="#dc3545"
            message="A error occured. Please try again later."
          />
        )}
        {loadingBabyName && (
          <Message color="DarkCyan" message="Loading name stats ....." />
        )}
        {babyName &&
          babyName.babies.map((baby) => (
            <SearchResult key={baby.id} baby={baby} />
          ))}
        {loadingBabyStats && (
          <Message color="DarkCyan" message="Loading ranking stats ....." />
        )}
        {babyStats && (
          <RankingResult
            highestRanking={babyStats[0]}
            averageRanking={babyStats[1]}
            gender={gender}
          />
        )}
        {loadingPopularity && (
          <Message color="DarkCyan" message="Loading popularity stats ....." />
        )}
        {popularityStats && (
          <PopularityResults popularityStats={popularityStats} />
        )}
      </section>
    </Fragment>
  );
}

export default SearchForm;
