import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
} from "recharts";
import classes from "./PopularityResults.module.css";

function formatGender(gender) {
  return gender.toLowerCase() === "neutral"
    ? "Neutral"
    : gender === "M"
    ? "Male"
    : "Female";
}

function PopularityResults({ popularityStats }) {
  const data = popularityStats.decades.map((data) => {
    return { name: data.decade, Ranking: data.ranking };
  });

  return (
    <div className={classes.main}>
      <h2 className={classes.heading}>
        Popularity Information for {popularityStats.name}
      </h2>
      <h3 className={classes.heading}>
        Gender: {formatGender(popularityStats.gender)}
      </h3>
      <LineChart width={800} height={400} data={data}>
        <Line type="monotone" dataKey="Ranking" stroke="#8884d8" />
        <CartesianGrid stroke="#ccc" />
        <Tooltip />
        <Legend />
        <XAxis dataKey="name" />
        <YAxis reversed={true} />
      </LineChart>
    </div>
  );
}

export default PopularityResults;
